import * as request from './useRequestInWorker.js'

const defaultJpg = 'data:image/png;base64,'

self.addEventListener('message', async (e) => {
  const { json, docsId, jpg, type, token } = e.data
  const { post } = request.default(token)
  if (type === 'sync') {
    await post(`/api/docs/${docsId}/update`, {
      json,
      previewImageUrl: jpg || defaultJpg,
    })
  }
})
